import Container from '@mui/material/Container';
import { Grid, Typography } from "@mui/material";
import SearchAppBar from "./components/Appbar";
import * as React from 'react';
import Button from '@mui/material/Button';
import Link from "@mui/material/Link";

function App() {
    return (
    <div className="LandingSell">
      <SearchAppBar />
      
      <Container md={{marginY: 5}} 
      >
          <img src="http://www.fmsconsult.com/images/5.jpg"
          alt=""
          className="img"
      />
      <Typography
          variant="h4"
          component="h2"
          marginTop={5}
          marginBottom={3}
        >
          ERP (Enterprise Resource Planning)
          </Typography>
        <Typography>
            ERP  Enterprise Resource Planning     ERP   ERP     
        </Typography>
        <Typography>
            
        </Typography>
        <Grid container spacing={3}>
        <Grid item xs={6}>
          <Link href="Register">
                <Button 
                type="register"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                >
              
                </Button>
          </Link>
                </Grid>


                <Grid item xs={6}>
                  <Link href="/Login">
                <Button 
                type="login"
                fullWidth
                variant="contained"
                sx={{ mt: 3, mb: 2 }}
                >
              
                </Button>
                </Link>
                </Grid>

        
        </Grid>
      </Container>
    </div>
    );
  }
  export default App;